import React, { useState, useEffect } from 'react'; // Import useState and useEffect
import { getDatabase, ref, onValue } from "firebase/database";
import firebase from "firebase/compat/app";
import "firebase/compat/database";
import InventoryList from './InventoryList';
import './App.css';
import logo from './logo.svg';

const firebaseConfig = {
  apiKey: "AIzaSyDt7OjeqsoclcoqKHoQjOUTz3uaD1Q7Ti4",
  authDomain: "easyinventory-22e9f.firebaseapp.com",
  databaseURL: "https://easyinventory-22e9f-default-rtdb.firebaseio.com",
  projectId: "easyinventory-22e9f",
  storageBucket: "easyinventory-22e9f.firebasestorage.app",
  messagingSenderId: "116855328042",
  appId: "1:116855328042:web:3c7982a74aa74ba8d16253",
  measurementId: "G-QKM2NXGXS6"
};

if (!firebase.apps.length) {
  firebase.initializeApp(firebaseConfig);
}

function App() {
  const [inventory, setInventory] = useState({}); 

  useEffect(() => {
    const db = getDatabase();
    const inventoryRef = ref(db, 'inventory');

    const unsubscribe = onValue(inventoryRef, (snapshot) => {
      const data = snapshot.val();
      setInventory(data || {});
    });

    return () => unsubscribe();
  },[]);

  return (
    <div className="App">
      <h1> </h1>
	  
      <InventoryList inventory={inventory} /> {  
	  }
    </div>
  );
}

export default App;